
      
import { LitElement, html, css } from 'lit-element';
import { transition, mark, slide } from 'lit-transition';

export class Comp extends LitElement {
  static get styles() { return css`
    .two {
      background: red;
    }`;
  }
  static get properties() {
    return { 
      a: Boolean, // to toggle content
      u: Boolean // effects unrelated content
    }
  }

  get unrelated() {
    return this.u ? 'A' : 'B';
  }

  render() {
    // transition wil only be triggered of the marked content
    // changes
    return html`
    <button @click=${() => this.a = !this.a}>change transitioned content</button>
    <button @click=${() => this.u = !this.u}>change unrelated content (should not transition)</button>
    <div>${transition(
      this.a ? mark(html`<div>ONE ${this.unrelated}</div>`,'one') :
        mark(html`<div class="two">TWO ${this.unrelated}</div>`,'two')
    , slide)}</div>`;
  } 
}

      export const code = "\nimport { LitElement, html, css } from 'lit-element';\nimport { transition, mark, slide } from 'lit-transition';\n\nexport class Comp extends LitElement {\n  static get styles() { return css`\n    .two {\n      background: red;\n    }`;\n  }\n  static get properties() {\n    return { \n      a: Boolean, // to toggle content\n      u: Boolean // effects unrelated content\n    }\n  }\n\n  get unrelated() {\n    return this.u ? 'A' : 'B';\n  }\n\n  render() {\n    // transition wil only be triggered of the marked content\n    // changes\n    return html`\n    <button @click=${() => this.a = !this.a}>change transitioned content</button>\n    <button @click=${() => this.u = !this.u}>change unrelated content (should not transition)</button>\n    <div>${transition(\n      this.a ? mark(html`<div>ONE ${this.unrelated}</div>`,'one') :\n        mark(html`<div class=\"two\">TWO ${this.unrelated}</div>`,'two')\n    , slide)}</div>`;\n  } \n}\n";
    